.about-header {
    background-image: url('/images/about/about-header.jpg') !important;
    background-color: rgb(0, 192, 146);
}

h2 {
    margin-bottom: 40px;
    @include font-size(27px);
}

p {
    @include font-size(19px);
}

.about-section {
    width: 70%;
    padding: 0;
    text-align: center;

    p {
        max-width: 47%;
        margin: auto;
        padding-top: 25px;
        word-wrap: break-word;
    }

    .underlined {
        border-bottom: 1px solid #444;
    }
}

.values {
    h2 {
        margin-bottom: 100px;
        @include font-size(32px);
        text-align: center;
    }

    [class*='col-'] {
        margin-bottom: 50px;
    }

    .value-heading {
        display: block;
        @include font-size(21px);
        font-weight: 300;
        color: #00c091;
        margin-bottom: 30px;
    }

    .value-desc {
        @include font-size(16px);
        color: rgba(255, 255, 255, 0.5);
    }
}

.mission {
    background: linear-gradient(rgba(0, 192, 146, 0.9), rgba(0, 192, 146, 0.9)),
    url('/images/about/about-mission.jpg') !important;
    padding-top: 165px !important;
    padding-bottom: 160px !important;
    background-size: cover !important;
    background-repeat: no-repeat;

    h1 {
        @include font-size(32px, true);
    }
}

@media (max-width: 768px) {
    .mission {
        padding-top: 70px !important;
        padding-bottom: 70px !important;
    }

    .mission h1 {
        @include font-size(28px, true);
    }
}

.why-we-exist {
    padding-top: 130px !important;
    padding-bottom: 130px !important;
}

@media (max-width: 768px) {
    .why-we-exist {
        padding-top: 50px !important;
        padding-bottom: 70px !important;
    }
}

#values {
    background: url('/images/about/about-values-background.jpg');
}

h1.green-section-heading {
    max-width: 80%;
    margin: auto;
    @include font-size(30px);
}

.about-exists-section {
    width: 70%;
    padding: 0;
    padding-bottom: 35px;
    text-align: center;

    p {
        text-align: justify;
        max-width: 65%;
        margin: auto;
        padding-top: 25px;
        word-wrap: break-word;
        line-height: 1.5;
    }
}

.members-section {
    width: 65%;
    padding: 0;
    text-align: center;
    @include font-size(16px);

    .member-details {
        visibility: hidden;
        @include opacity(0);
        overflow-y: scroll;
        padding: 30px;
        width: 100%;
        height: 100%;
        background-color: #fff;
        border: 1px solid #c6c6c6;
        border-radius: 5px;
        text-align: left;
        position: absolute;
        top: 0;
        left: 0;
        margin: auto;
        @include transition(0.2s);

        p {
            @include font-size(16px);
            color: #333;
            @include transition(0.2s);
        }

        &::-webkit-scrollbar {
            width: 8px;
        }
        &::-webkit-scrollbar-track {
            background-color: transparent;
            padding-right: 10px;
        }
        &::-webkit-scrollbar-thumb {
            background-color: rgba(0, 0, 0, 0.10);
            border-radius: 5px;
        }
        &::-webkit-scrollbar-thumb:hover {
            background-color: rgba(0, 0, 0, 0.15);
        }
    }

    .member-photo {
        margin: 20px auto 35px auto;
        width: 130px;
        height: 130px;
        border-radius: 100%;
        background-position: center;
        background-size: cover;
        background-repeat: no-repeat;
        @include transition(0.2s);
    }

    .member-name {
        margin-bottom: 5px;
        @include font-size(21px);
        color: #00c092;
        @include transition(0.2s);
    }

    .member-job-title {
        @include font-size(16px);
        color: #adadad;
        @include transition(0.15s);
    }
}

.join-our-team-section {
    padding: 50px;

    h1 {
        @include font-size(32px);
    }

    .open-job-positions {
        margin-top: 20px;
        line-height: 22px;
        border: 0;
        padding: 13px 32px;
    }

    .open-job-positions:focus {
        outline: 0;
    }

    @media (max-width: 768px) {
        & {
            padding: 0;
        }
    }
}

.fit-here-section {
    img {
        width: 100%;
    }

    .you-fit-here {
        color: #fff;
        position: absolute;
        top: 43%;
        left: 23%;

        a {
            display: block;
            padding: 20px 30px;
            background-color: #00c091;
            border-radius: 5px;
            @include font-size(21px);
            color: inherit;
            text-decoration: none;
            @include transition(0.2s);

            &:after {
                top: 100%;
                left: 50%;
                border: solid transparent;
                content: " ";
                height: 0;
                width: 0;
                position: absolute;
                pointer-events: none;
                border-color: rgba(0, 192, 145, 0);
                border-top-color: #00c091;
                border-width: 10px;
                margin-left: -10px;
            }

            &:focus,
            &:active {
                outline: 0;
            }

            &:hover {
                @include transform(translateY(-10px));
            }
        }
    }
}

@media (max-width: 800px) {
    .about-exists-section {
        width: 100% !important;
        padding: 0 2%;
    }

    p {
        max-width: 100% !important;
    }
}

@media (max-width: 768px) {
    .fit-here-section .you-fit-here a {
        @include font-size(18px);
        padding: 10px 20px;
    }
}

@media (max-width: 550px) {
    .fit-here-section .you-fit-here {
        top: 35%;
        left: 18%;
    }
}

@media (max-width: 768px) {
    .fit-here-section .you-fit-here {
        display: none;
    }
}

@media (max-width: 390px) {
    .fit-here-section .you-fit-here {
        top: 25%;
        left: 10%;
    }
}
